import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
// import { FixReqestComponent } from 'src/app/components/fix-reqest/fix-reqest.component';
import { AppointmentInfoComponent } from '../components/appointment-info/appointment-info.component';
import { ContactInfoProblemComponent } from '../components/contact-info-problem/contact-info-problem.component';
import { FixRequestStepsLayoutComponent } from '../components/fix-request-steps-layout/fix-request-steps-layout.component';
import { ProblemClassComponent } from '../components/problem-class/problem-class.component';
import { ThankYouComponent } from '../components/thank-you/thank-you.component';
import { TrackRequestComponent } from '../components/track-request/track-request.component';
import { FixRequestComponent } from '../fix-request.component';


const routes: Routes = [

  { path: '',  component: FixRequestComponent,},

  {path: 'steps' ,component: FixRequestStepsLayoutComponent
   , children: [
    { path: 'problem',  component: ProblemClassComponent, data: { breadcrumb: 'Problem'} },
    { path: 'contact',  component: ContactInfoProblemComponent, data: { breadcrumb: 'Contact'}},
    { path: 'appointment',  component: AppointmentInfoComponent, data: { breadcrumb: 'Appointment'} },
  ]},
  { path: 'thank-you',  component: ThankYouComponent },
  { path: 'track-request',  component: TrackRequestComponent },
// { path: 'fix',  component: FixRequestComponent, pathMatch:'full' },

];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FixRequestRoutingModule { }
