import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';


@Component({
  selector: 'app-contact-info-problem',
  templateUrl: './contact-info-problem.component.html',
  styleUrls: ['./contact-info-problem.component.scss']
})
export class ContactInfoProblemComponent implements OnInit {
  contactForm: FormGroup ;

  constructor(private fb: FormBuilder , private router: Router ) { }

  ngOnInit(): void {
    this.createcontactForm();

  }
  createcontactForm() {
    this.contactForm = this.fb.group({
      userName: new FormControl('', [Validators.required]),
      LastName: new FormControl('', [Validators.required]),
      email: new FormControl('', [Validators.required , Validators.email]),
      mobileNumber: new FormControl('', Validators.required)
    });
  }
  saveData() {
    if(this.contactForm.valid) {
      let formValue= this.contactForm.controls.userName.value
      this.router.navigate(['fix-request/appointment']);
    } else {
      this.contactForm.markAllAsTouched();
    }


  }

  checkValidation() {
    if(this.contactForm.valid) {
      return this.contactForm.value;
    } else {
      return 'notvalid';
    }
  }
}
