import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { FixRequestRoutingModule } from './fix-request-routing.module';
import { FixRequestComponent } from '../fix-request.component';
import { AppointmentInfoComponent } from '../components/appointment-info/appointment-info.component';
import { ChooseDivceComponent } from '../components/choose-divce/choose-divce.component';
import { ContactInfoProblemComponent } from '../components/contact-info-problem/contact-info-problem.component';
import { ProblemClassComponent } from '../components/problem-class/problem-class.component';
import { ThankYouComponent } from '../components/thank-you/thank-you.component';
import { MatSelectModule } from '@angular/material/select';
import { ReactiveFormsModule } from '@angular/forms';
import { MatFormFieldModule } from '@angular/material/form-field';
import { MatInputModule } from '@angular/material/Input';
import { MatCheckboxModule } from '@angular/material/checkbox';
import { MatButtonModule } from '@angular/material/button';
import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatNativeDateModule, MatRippleModule, } from '@angular/material/core';
import { TrackRequestComponent } from '../components/track-request/track-request.component';
import { MatStepperModule } from '@angular/material/stepper';
import { MatIconModule } from '@angular/material/icon';
import { FixRequestStepsLayoutComponent } from '../components/fix-request-steps-layout/fix-request-steps-layout.component';
import { HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';



// import { MatMomentDateModule } from "@angular/material-moment-adapter";

@NgModule({
  declarations: [
    FixRequestComponent,
    AppointmentInfoComponent,
    ChooseDivceComponent,
    ContactInfoProblemComponent,
    ProblemClassComponent,
    ThankYouComponent,
    TrackRequestComponent,
  ],
  imports: [
    CommonModule,
    FixRequestRoutingModule,
    MatSelectModule,
    ReactiveFormsModule,
    MatFormFieldModule,
    MatInputModule,
    MatCheckboxModule,
    MatButtonModule,
    MatDatepickerModule,
    MatIconModule,
    MatNativeDateModule,
    MatRippleModule,
    MatStepperModule,
    TranslateModule.forChild({
      loader: {
        provide: TranslateLoader,
        useFactory: httpTranslateLoader,
        deps: [HttpClient]
      }
    })
  ]
})
export class FixRequestModule { }

// AOT compilation support
export function httpTranslateLoader(http: HttpClient) {
  return new TranslateHttpLoader(http);
}

