import { Component, OnInit } from '@angular/core';
import { FixRequestService } from '../../services/fix-request.service';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { FixRequestDataService } from '../../services/fix-request-data.service';
import { AuthService } from 'src/app/core/services';

@Component({
  selector: 'app-choose-divce',
  templateUrl: './choose-divce.component.html',
  styleUrls: ['./choose-divce.component.scss']
})
export class ChooseDivceComponent implements OnInit {
  brandFirstTime = true
  modelFirstTime = true
  fixBrands = [];
  fixModels = [];
  chooseDeviceForm: FormGroup;

  constructor(
    private _fixServ: FixRequestService,
    private _fixServData: FixRequestDataService,
    private fb: FormBuilder,
    private router: Router,
    private authSrvc: AuthService
  ) { }

  ngOnInit(): void {
    this.createChooseDeviceForm()
    this.getFixBrands()

    this.getRole();
    this.getIsVerified()
  }
  createChooseDeviceForm() {
    this.chooseDeviceForm = this.fb.group({
      deviceBrand: new FormControl('', [Validators.required]),
      deviceModel: new FormControl('', [Validators.required]),
    });
  }

  getFixBrands() {
    this._fixServ.getAllBrands().subscribe((response) => {
      // console.log('brands', response.data);
      this.fixBrands = response.data
      // debugger
      if (this._fixServData.fixRequestData.brandId && this.brandFirstTime) {
        this.chooseDeviceForm.patchValue({
          deviceBrand: this._fixServData.fixRequestData.brandId || null
        })
        this.getFixModelsById(this._fixServData.fixRequestData.brandId)
        this.brandFirstTime = false
      }
    })
  }
  getFixModelsById(brandId) {
    this.fixModels = [];
    this._fixServ.getAllModelsById(brandId).subscribe((response: any) => {
      if (response.succeeded) {
        this.chooseDeviceForm.controls["deviceModel"].setValue("");
        this.fixModels = response.data
        if (this._fixServData.fixRequestData.modelId && this.modelFirstTime) {
          this.chooseDeviceForm.patchValue({
            deviceModel: this._fixServData.fixRequestData.modelId || null,
          })
          this.modelFirstTime = false
        }
      }
    })
  }


  getBrandValues(e) {
    let brandId = e.value
    if (brandId) {
      this.getFixModelsById(brandId)
    } else {
      this.chooseDeviceForm.controls["deviceModel"].setValue("");
    }
  }




  nextStep() {
    // debugger;
    if (!this.role && !this.IsVerified) {
      this.authSrvc.removeUserData();
      return this.router.navigate(['register'], { replaceUrl: true })
    }
    let modelDeviceId = this.chooseDeviceForm.controls.deviceModel.value
    let BrandDeviceId = this.chooseDeviceForm.controls.deviceBrand.value
    this._fixServData.fixRequestData.modelId = modelDeviceId;
    this._fixServData.fixRequestData.brandId = BrandDeviceId;
    if (this.chooseDeviceForm.valid) {

      this._fixServData.breadcrumbLinks[0]['label'] =
        this._fixServData.getDataFromArray(modelDeviceId, this.fixModels)['name']
      this._fixServData.breadcrumbLinks[1]['label'] =
        this._fixServData.getDataFromArray(BrandDeviceId, this.fixBrands)['name']

      this.router.navigate(['fix-request/steps/problem', { id: modelDeviceId }])
      // this.router.navigate(['fix-request/problem'])
    } else {
      this.chooseDeviceForm.markAllAsTouched();
    }
  }

  getModelValues(e) {
    // console.log('e', e);
  }

  // *ngIf="(role==='bussiness' && IsVerified)"

  role: string = ""
  getRole() {
    this.role = this.authSrvc.role
  }
  IsVerified: boolean
  getIsVerified() {
    this.IsVerified = this.authSrvc.isVerified
  }

}
