import { Component, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { FixRequestDataService } from '../../services/fix-request-data.service';
import { FixRequestService } from '../../services/fix-request.service';
import { ProblemClassComponent } from '../problem-class/problem-class.component';
import * as moment from "moment";
import { DatePipe } from '@angular/common';
@Component({
  selector: 'app-appointment-info',
  templateUrl: './appointment-info.component.html',
  styleUrls: ['./appointment-info.component.scss']
})
export class AppointmentInfoComponent implements OnInit {
  ApppointmentForm: FormGroup;
  userid = localStorage.getItem('id')
  todayDate = new Date();
  branshs = []
  times = []
  problemId;
  constructor(private fb: FormBuilder,
    private _fixServ: FixRequestService,
    private _fixServData: FixRequestDataService,
    private route: ActivatedRoute,
    private router: Router) {
    this._fixServData.filterLinks(4)
    this.problemId = this.route.snapshot.paramMap.get('id')
    this._fixServData.breadcrumbLinks[2].queryParam = { id: this.problemId }
  }

  ngOnInit(): void {
    // this.checkdata()
    this.createProblemForm();
    this.getBranches()
    this.generateTimes()


  }

  checkdata() {
    // console.log(this._fixServData.fixData.modelId);

    if (this._fixServData.fixRequestData.modelId) {
      return;
    } else {
      this.router.navigate(['/fix-request']);
    }
  }

  createProblemForm() {
    this.ApppointmentForm = this.fb.group({
      chooseBranch: new FormControl('', [Validators.required]),
      appointmentDate: new FormControl('', [Validators.required]),
      time: new FormControl('', [Validators.required]),
    });
  }

  generateTimes() {
    var x = 15; //minutes interval
    // var times = []; // time array
    var tt = 0; // start time
    var ap = ['AM', 'PM']; // AM-PM

    //loop to increment the time and push results in array
    for (var i = 0; tt < 24 * 60; i++) {
      var hh = Math.floor(tt / 60); // getting hours of day in 0-24 format
      var mm = (tt % 60); // getting minutes of the hour in 0-55 format
      this.times[i] = ("0" + (hh % 12)).slice(-2) + ':' + ("0" + mm).slice(-2) + ap[Math.floor(hh / 12)]; // pushing data in array in [00:00 - 12:00 AM/PM format]
      tt = tt + x;
    }
    // console.log(this.times);

  }


  formattingDateToclient(date) {
    // debugger;
    if (!(date == null || date == "")) {
      const formatedDate = moment(date, "DD/MM/YYYY", true);
      // console.log('formated', formatedDate)
      return formatedDate;
    }
    return "";
  }

  getBranches() {
    this._fixServ.getAllBranshes().subscribe((response) => {
      console.log('response', response);
      this.branshs = response.data
      if (this._fixServData.fixRequestData.branchId) {
        this.ApppointmentForm.patchValue({
          chooseBranch: this._fixServData.fixRequestData.branchId
        })
      }
    })
  }
  hours;
  min;
  saveData() {
    // console.log(this.userid);
    let appoinetDate = this.ApppointmentForm.controls.appointmentDate.value
    let appoinetTime: string = this.ApppointmentForm.controls.time.value

    // debugger;
    this.hours = Number(appoinetTime.slice(0, 2))
    this.min = Number(appoinetTime.slice(3, 5))
    if (appoinetTime.includes('PM')) {
      this.hours += 12
    }
    if (this.hours < 10) {
      this.hours = '0' + this.hours;
    }
    if (this.min < 10) {
      this.min = '0' + this.min;
    }
    let year = appoinetDate.getFullYear()
    let month = appoinetDate.getMonth() + 1
    let day = appoinetDate.getDate()
    console.log(`${year}-${month}-${day}T${this.hours}:${this.min}:00.000Z`);
    // let DateCollection = `${year}-${month}-${day}T${this.hours}:${this.min}:17.615Z`
    let DateCollection = appoinetDate.toISOString()
    if (this.ApppointmentForm.valid) {

      let userData = {
        "userId": this.userid,
        "branchId": Number(this.ApppointmentForm.controls.chooseBranch.value), // appointment component
        // "appointment": '2020-09-29T08:48:17.615Z', // concat between datetimepicker and time
        "appointment": DateCollection, // concat between datetimepicker and time
        "status": 1, // still = 1
        "deviceProblemClassificationId": Number(this.problemId), // problem component
        "amount": 1 // still = 1
      }

      this._fixServ.checkAppointment(userData).subscribe((response) => {
        if (response.succeeded) {
          let requestId = response.data
          setTimeout(() => {
            this.router.navigate(['fix-request/thank-you', { requestId }]);
          }, 500);
        }
      })
    } else {
      this.ApppointmentForm.markAllAsTouched();
    }


  }




}
