import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { FixRequestDataService } from '../../services/fix-request-data.service';
import { FixRequestService } from '../../services/fix-request.service';

@Component({
  selector: 'app-problem-class',
  templateUrl: './problem-class.component.html',
  styleUrls: ['./problem-class.component.scss']
})
export class ProblemClassComponent implements OnInit {
  fixForm: FormGroup ;
  deviceModelId;
  fixProblem = []
  // modelProblems = []

  constructor(  private fb: FormBuilder ,
    private _fixServ: FixRequestService,
    private _fixServData: FixRequestDataService,
    private route: ActivatedRoute,
    private router: Router) {
      this._fixServData.filterLinks(3)
    }

    ngOnInit(): void {
      this.checkdata()
      this.createProblemForm();

      this.deviceModelId = this.route.snapshot.paramMap.get('id')
      this.getProblems(this.deviceModelId)
      if( this._fixServData.fixRequestData.deviceProblemClassificationId ) {
        this.fixForm.patchValue({
          fixProblem: this._fixServData.fixRequestData.deviceProblemClassificationId ||null
        })
      }
  }


  checkdata() {
    // console.log(this._fixServData.fixData.modelId);

    if(this._fixServData.fixRequestData.modelId) {
      return;
    } else {
      this.router.navigate(['/fix-request']);
    }
  }


  createProblemForm() {
    this.fixForm = this.fb.group({
      fixProblem: new FormControl('', [Validators.required]),

    });
  }


  getProblems(id) {
    this._fixServ.getAllProblems(id).subscribe((response) => {
      this.fixProblem = response.data
      // console.log('problem', response.data);
    })
  }

  saveData() {

    if(this.fixForm.valid) {
      let problemId = this.fixForm.controls.fixProblem.value
      this._fixServData.fixRequestData.deviceProblemClassificationId = problemId
      this.router.navigate(['fix-request/steps/appointment',{id: problemId} ]);
    } else {
      this.fixForm.markAllAsTouched();
    }


  }

  checkValidation() {
    if(this.fixForm.valid) {
      return this.fixForm.controls.fixProblem.value;
    } else {
      return 'notvalid';
    }
  }
}
