import { Component, OnInit, ViewChild } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { MatStepper } from '@angular/material/stepper';
import { ActivatedRoute } from '@angular/router';
import { FixRequestService } from '../../services/fix-request.service';

@Component({
  selector: 'app-track-request',
  templateUrl: './track-request.component.html',
  styleUrls: ['./track-request.component.scss']
})
export class TrackRequestComponent implements OnInit {
  isLinear = false;
  stepOneIsCompleted = false;
  stepTwoIsCompleted = false;
  stepThreeIsCompleted = false;

  requestId;
  requestData= [];
  stepComplete= false;
  get requestIdCtrl() { return this.myForm.get('requestId') }
  myForm: FormGroup
  @ViewChild('stepper', { static: false }) private stepper: MatStepper;
  constructor(
    private fb: FormBuilder,
    private _fixServ: FixRequestService,
    private route: ActivatedRoute,
    ) {}

  ngOnInit() {
    this.requestId = this.route.snapshot.paramMap.get('requestId');
    console.log(this.requestId);
    if(this.requestId) {
      this.createRequestIdForm();
      this.requestIdCtrl.setValue(this.requestId)
      this.getAllTracks();
    } else {
      this.createRequestIdForm();
    }

  }

  createRequestIdForm() {
    this.myForm = this.fb.group(
      {
        requestId: [""],
      })
    }


    requestIdSearch(formData) {
      console.log(formData);
      this._fixServ.getAllTrackRequest(formData.value.requestId)
      .subscribe((res) => {
      this.requestData = res.data
      console.log('here', this.requestData);
      this.requestData.map((request) => {
        this.stepper.selected.completed = true;
        for(let i = 1; i < request.newStatus; i++) {
          this.stepper.next();
        }
      })
    })
  }




  getAllTracks() {
    this._fixServ.getAllTrackRequest(this.requestId)
    .subscribe((res) => {
      this.requestData = res.data
      console.log('here', this.requestData);
      this.requestData.map((request) => {
        this.stepper.selected.completed = true;
        for(let i = 1; i < request.newStatus; i++) {

          this.stepper.next();
        }
      })
    })
  }

}
